import React from "react";
import { motion } from "framer-motion";

type FadeAwayValueProps = {
  duration?: number;
  children: React.ReactNode;
};
const FadeAwayValue: React.FC<FadeAwayValueProps> = ({
  duration = 0.5,
  children,
}) => {
  return (
    <motion.div
      initial={{
        scale: 0.1,
        y: -20,
      }}
      animate={{
        scale: 2,
        y: -80,
        opacity: 0,
      }}
      transition={{ duration }}
      style={{ position: "absolute" }}
    >
      {children}
    </motion.div>
  );
};

export default FadeAwayValue;
