import { Action, ActionHandlers, EffectHandlers } from "~/logic/model";
import Fight, { FightTarget } from "./fight.model";

import Hooks, { actions as hookActions, effects as hookEffects } from "./hooks.model";

/**
 * Module for entity state. An entity is something that has HP and other
 * attributes. This usually means either the player or the enemy. Provides
 * actions/effects for setting an entity's attributes.
 */
declare namespace Entity {
  type Attributes = {
    hp: number;
    certainty: number;
  };

  type Stance = {
    name: string;
    hooks: Hooks.Hook[];
  };

  type Power = {
    name: string;
    hooks: Hooks.Hook[];
  };

  type State = Hooks.State & {
    attributes: Attributes;
    stance: Stance | null;
    powers: Power[];
  };

  type Actions = Hooks.Actions & {
    // Sets the given attribute for the given target to the given value
    SET_ATTRIBUTE: Action<
      "SET_ATTRIBUTE",
      {
        target: FightTarget;
        attribute: keyof Attributes;
        value: number;
      }
    >;
    // Sets the entity's stance
    SET_STANCE: Action<
      "SET_STANCE",
      {
        target: FightTarget;
        stance: Stance | null;
      }
    >;
    // Adds a power to the entity
    ADD_POWER: Action<
      "ADD_POWER",
      {
        target: FightTarget;
        power: Power;
      }
    >;
    // Removes power from entity
    REMOVE_POWER: Action<
      "REMOVE_POWER",
      {
        target: FightTarget;
        name: Power["name"];
      }
    >;
  };

  type Effects = Hooks.Effects & {
    // Deals damage to the given target
    DEAL_DAMAGE: Action<
      "DEAL_DAMAGE",
      {
        target: FightTarget;
        damage: number;
      }
    >;
    // Gains certainty on the given target
    GAIN_CERTAINTY: Action<
      "GAIN_CERTAINTY",
      {
        target: FightTarget;
        certainty: number;
      }
    >;
    // Clears the certainty of the given target
    CLEAR_CERTAINTY: Action<
      "CLEAR_CERTAINTY",
      {
        target: FightTarget;
      }
    >;
    // Sets the target's stance
    ENTER_STANCE: Action<
      "ENTER_STANCE",
      {
        target: FightTarget;
        stance: Stance;
      }
    >;
    // Clears the target's stance
    EXIT_STANCE: Action<
      "EXIT_STANCE",
      {
        target: FightTarget;
      }
    >;
    ADD_POWER: Actions["ADD_POWER"];
  };
}

export const actions: ActionHandlers<Fight.State, Entity.Actions> = {
  ...hookActions,
  SET_ATTRIBUTE: (state, { target, attribute, value }) => {
    return {
      ...state,
      [target]: {
        ...state[target],
        attributes: {
          ...state[target].attributes,
          [attribute]: value,
        },
      },
    };
  },

  SET_STANCE: (state, { target, stance }) => {
    return {
      ...state,
      [target]: {
        ...state[target],
        stance,
      },
    };
  },

  ADD_POWER: (state, { target, power }) => {
    return {
      ...state,
      [target]: {
        ...state[target],
        powers: [...state[target].powers, power],
      },
    };
  },

  REMOVE_POWER: (state, { target, name }) => {
    return {
      ...state,
      [target]: {
        ...state[target],
        powers: state[target].powers.filter((power) => power.name !== name),
      },
    };
  },
};

export const effects: EffectHandlers<
  Fight.State,
  Fight.Actions,
  Fight.Effects
> = {
  ...hookEffects,
  DEAL_DAMAGE: async ({ data: { target, damage }, state, dispatch }) => {
    const targetCertainty = state[target].attributes.certainty;
    const unblockedDamage = damage - targetCertainty;

    if (targetCertainty > 0) {
      await dispatch("SET_ATTRIBUTE", {
        target,
        value: Math.max(state[target].attributes.certainty - damage, 0),
        attribute: "certainty",
      });
    }

    if (unblockedDamage >= 0) {
      await dispatch("SET_ATTRIBUTE", {
        target,
        value: state[target].attributes.hp - unblockedDamage,
        attribute: "hp",
      });
    }
  },

  GAIN_CERTAINTY: async ({ data: { target, certainty }, state, dispatch }) => {
    await dispatch("SET_ATTRIBUTE", {
      target,
      value: state[target].attributes.certainty + certainty,
      attribute: "certainty",
    });
  },

  CLEAR_CERTAINTY: async ({ data: { target }, dispatch }) => {
    await dispatch("SET_ATTRIBUTE", {
      target,
      value: 0,
      attribute: "certainty",
    });
  },

  ENTER_STANCE: async ({ data: { target, stance }, dispatch }) => {
    await dispatch("SET_STANCE", {
      target,
      stance,
    });
    for (const hook of stance.hooks) {
      await dispatch("ADD_HOOK", {
        target,
        hook,
      });
    }
  },

  EXIT_STANCE: async ({ data: { target }, dispatch, state }) => {
    const { stance } = state[target];
    if (stance) {
      for (const hook of stance.hooks) {
        await dispatch("REMOVE_HOOK", {
          target,
          hookId: hook.id,
        });
      }
      await dispatch("SET_STANCE", {
        target,
        stance: null,
      });
    }
  },

  ADD_POWER: async ({ data: { target, power }, dispatch }) => {
    await dispatch("ADD_POWER", {
      target,
      power,
    });
    for (const hook of power.hooks) {
      await dispatch("ADD_HOOK", {
        target,
        hook,
      });
    }
  },
};

export default Entity;
