import * as React from "react";
import styled from "styled-components";
import { AnimateSharedLayout } from "framer-motion";

import { useModel } from "~/logic/ModelProvider";
import { initFight } from "~/fight/FightModel";
import FightContext from "~/fight/FightContext";
import { defaultPlayer } from "~/game/player/default";
import { createDescartes } from "~/game/ai/descartes";

import FightStageHeader from "./FightStageHeader";
import FightStageFooter from "./FightStageFooter";
import PlayerStage from "./PlayerStage";
import EnemyStage from "./EnemyStage";
import HandStage from "./HandStage";
import VictoryModal from "./VictoryModal";
import DefeatModal from "./DefeatModal";

const fight = initFight(defaultPlayer(), createDescartes());

const FightStage: React.FC = () => {
  const [state, model] = useModel(fight);
  React.useEffect(() => {
    model.affect("START_FIGHT", {});
    model.run();
  }, []);

  const victory = state.enemy.attributes.hp <= 0;
  const defeat = state.player.attributes.hp <= 0;

  return (
    <FightContext.Provider value={[state, model]}>
      <AnimateSharedLayout>
        <FightStageWrapper>
          <FightStageHeaderWrapper>
            <FightStageHeader />
          </FightStageHeaderWrapper>
          <FightStageContentWrapper>
            <PlayerStage />
            <EnemyStage />
          </FightStageContentWrapper>
          <HandStage />
          <FightStageFooterWrapper>
            <FightStageFooter />
          </FightStageFooterWrapper>
          <VictoryModal open={victory} />
          <DefeatModal open={defeat} />
        </FightStageWrapper>
      </AnimateSharedLayout>
    </FightContext.Provider>
  );
};

const FightStageWrapper = styled.div`
  padding: 32px;
  padding-bottom: 0;
  display: flex;
  flex-direction: column;
  height: calc(100vh - 32px);
`;

const FightStageHeaderWrapper = styled.header`
  flex: 0;
`;

const FightStageContentWrapper = styled.div`
  flex: 1;
  display: flex;
  justify-content: space-between;
`;

const FightStageFooterWrapper = styled.div`
  flex: 0;
  z-index: 1;
`;

export default FightStage;
