import React from "react";
import ReactTooltip from "react-tooltip";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";
import { FightTarget } from "~/fight/fight.model";
import Entity from "~/fight/entity.model";
import Cogito from "~/assets/cogito.png";
import HooksText from "../ui/HooksText";

const POWER_ICONS: Map<string, string> = new Map([["Cogito", Cogito]]);

type EntityPowersProps = {
  target: FightTarget;
};
const EntityPowers: React.FC<EntityPowersProps> = ({ target }) => {
  const [state] = useFight();
  const { powers } = state[target];

  return (
    <>
      {powers.map((power, i) => {
        const id = `${target}:${power.name}:${i}`;
        const icon = POWER_ICONS.get(power.name) || "";

        return (
          <React.Fragment key={id}>
            <ReactTooltip
              id={id}
              effect="solid"
              place="bottom"
              backgroundColor="white"
              textColor="#333333"
              multiline
            >
              {/* <MetaTitle>Power</MetaTitle> */}
              <PowerName>{power.name}</PowerName>
              <HooksText target="enemy" hooks={power.hooks} />
            </ReactTooltip>
            <PowerIcon
              data-tip
              data-for={id}
              style={{ backgroundImage: `url(${icon})` }}
            />
          </React.Fragment>
        );
      })}
    </>
  );
};

const MetaTitle = styled.div`
  text-align: center;
`;

const PowerName = styled.div`
  font-weight: bold;
  text-align: center;
`;

const PowerIcon = styled.div`
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 5px;
  border-radius: 100%;
  background-size: cover;
  background-position: center;
`;

export default EntityPowers;
