import { Action, ActionHandlers, EffectHandlers } from "~/logic/model";
import Fight, { FightTarget } from "./fight.model";

/**
 * Module for hooks. Hooks are attached to different parts of the fight state
 * and run at specific times. They have a probability of running, and if
 * successful, they dispatch all effects on the hook.
 */
declare namespace Hooks {
  type HookTurnDefn = Array<number> | ["before" | "after", number]
  type Hook = {
    id: number;
    p: number;
    when: "start-of-turn" | "end-of-turn";
    turns: HookTurnDefn;
    effects: Fight.Effects[keyof Fight.Effects][];
  };

  type State = {
    hooks: Hook[];
  };

  type Actions = {
    // Adds a new hook to the state
    ADD_HOOK: Action<
      "ADD_HOOK",
      {
        target: FightTarget;
        hook: Hook;
      }
    >;
    // Removes a hook from the state
    REMOVE_HOOK: Action<
      "REMOVE_HOOK",
      {
        target: FightTarget;
        hookId: Hook["id"];
      }
    >;
  };

  type Effects = {
    // Runs through all given hooks
    RUN_HOOKS: Action<
      "RUN_HOOKS",
      {
        hooks: Hook[];
      }
    >;
    ADD_HOOK: Actions["ADD_HOOK"];
    REMOVE_HOOK: Actions["REMOVE_HOOK"];
  };
}

export const actions: ActionHandlers<Fight.State, Fight.Actions> = {
  ADD_HOOK: (state, { target, hook }) => {
    return {
      ...state,
      [target]: {
        ...state[target],
        hooks: [...state[target].hooks, hook],
      },
    };
  },

  REMOVE_HOOK: (state, { target, hookId }) => {
    return {
      ...state,
      [target]: {
        ...state[target],
        hooks: state[target].hooks.filter((hook) => hook.id !== hookId),
      },
    };
  },
};

export const effects: EffectHandlers<
  Fight.State,
  Fight.Actions,
  Fight.Effects
> = {
  RUN_HOOKS: async ({ data: { hooks }, affect, state }) => {
    for (const hook of hooks) {
      let isScripted = false
      switch (hook.turns[0]) {
        case "before":
          isScripted = state.turn < hook.turns[1]
          break;
        case "after":
          isScripted = state.turn > hook.turns[1]
          break;
        default:
          isScripted = !!(hook.turns as number[]).find((turn) => turn === state.turn);
      }
      if (hook.turns.length > 0 && !isScripted) {
        continue;
      }

      const isSuccess = hook.p >= Math.random();
      const shouldRun = isScripted ? isScripted && isSuccess : isSuccess;

      if (shouldRun) {
        for (const effect of hook.effects) {
          await affect(effect.name, effect.data);
        }
      }
    }
  },

  ADD_HOOK: async ({ data, dispatch }) => {
    await dispatch("ADD_HOOK", data);
  },

  REMOVE_HOOK: async ({ data, dispatch }) => {
    await dispatch("REMOVE_HOOK", data);
  },
};

export default Hooks;
