import * as React from "react";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";

const FightStageHeader: React.FC = () => {
  const [{ turn }] = useFight();
  return <Wrapper>{turn ? <TurnText>Turn {turn}</TurnText> : null}</Wrapper>;
};

const Wrapper = styled.div`
  display: flex;
`;

const TurnText = styled.div`
  background: rgba(0, 0, 0, 0.66);
  padding: 10px;
  border-radius: 4px;
`;

export default FightStageHeader;
