import { shuffle } from "lodash";
import { Action, ActionHandlers, EffectHandlers } from "~/logic/model";
import Fight from "./fight.model";

type CardEffect =
  | Fight.Effects["DRAW_CARDS"]
  | Fight.Effects["DEAL_DAMAGE"]
  | Fight.Effects["GAIN_CERTAINTY"]
  | Fight.Effects["EXHAUST_CARD"];

export type Card = {
  id: number;
  name: string;
  type: "attack" | "skill" | "curse";
  effects: CardEffect[];
  cost: number;
};

type CardCreator = () => Card;

/**
 * Module for card piles. Provides actions/effects for drawing, discarding,
 * exhausting, shuffling, and spawning new cards.
 */
declare namespace CardPiles {
  type State = {
    cards: {
      hand: Card[];
      draw: Card[];
      discard: Card[];
      exhaust: Card[];
    };
  };

  type Actions = {
    // Attempts to draw a single card from the draw pile
    DRAW_CARD: Action<"DRAW_CARD">;
    // Attempts to move a given card from one pile to another
    MOVE_CARD: Action<
      "MOVE_CARD",
      {
        card: Card;
        from: keyof CardPiles.State["cards"];
        to: keyof CardPiles.State["cards"];
      }
    >;
    // Shuffles all cards from one pile to another
    SHUFFLE_CARDS: Action<
      "SHUFFLE_CARDS",
      {
        kind: "discard-to-draw";
      }
    >;
    // Spawns a new card in a given pile
    SPAWN_CARD: Action<
      "SPAWN_CARD",
      {
        card: Card;
        pile: keyof CardPiles.State["cards"];
      }
    >;
  };

  type Effects = {
    // Draws `nCards`, shuffling discard-to-draw if the draw pile is empty
    DRAW_CARDS: Action<
      "DRAW_CARDS",
      {
        nCards: number;
      }
    >;
    // Discards a given card
    DISCARD_CARD: Action<
      "DISCARD_CARD",
      {
        cardId: Card["id"];
      }
    >;
    // Exhausts a given card
    EXHAUST_CARD: Action<
      "EXHAUST_CARD",
      {
        cardId: Card["id"];
      }
    >;
    // Spawns a new card in a given pile
    ADD_CARD: Action<
      "ADD_CARD",
      {
        card: () => Card;
        pile: keyof CardPiles.State["cards"];
      }
    >;
  };
}

export const actions: ActionHandlers<Fight.State, Fight.Actions> = {
  DRAW_CARD: (state) => {
    const card = state.player.cards.draw[state.player.cards.draw.length - 1];
    if (card) {
      return {
        ...state,
        player: {
          ...state.player,
          cards: {
            ...state.player.cards,
            hand: [...state.player.cards.hand, card],
            draw: state.player.cards.draw.filter((c) => c.id !== card.id),
          },
        },
      };
    } else {
      return false;
    }
  },

  MOVE_CARD: (state, { card, from, to }) => {
    const newFromPile = state.player.cards[from].filter(
      (c) => c.id !== card.id
    );
    if (newFromPile.length === state.player.cards[from].length) {
      return false;
    }
    return {
      ...state,
      player: {
        ...state.player,
        cards: {
          ...state.player.cards,
          [from]: newFromPile,
          [to]: [...state.player.cards[to], card],
        },
      },
    };
  },

  SHUFFLE_CARDS: (state) => {
    return {
      ...state,
      player: {
        ...state.player,
        cards: {
          ...state.player.cards,
          draw: shuffle(state.player.cards.discard),
          discard: [],
        },
      },
    };
  },

  SPAWN_CARD: (state, { card, pile }) => {
    return {
      ...state,
      player: {
        ...state.player,
        cards: {
          ...state.player.cards,
          [pile]: shuffle([...state.player.cards[pile], card]),
        },
      },
    };
  },
};

export const effects: EffectHandlers<
  Fight.State,
  Fight.Actions,
  Fight.Effects
> = {
  DRAW_CARDS: async ({ data: { nCards = 1 }, dispatch }) => {
    let nCardsDrawn = 0;
    while (nCardsDrawn < nCards) {
      const successState = await dispatch("DRAW_CARD", {});
      if (successState) {
        nCardsDrawn++;
      } else {
        await dispatch("SHUFFLE_CARDS", { kind: "discard-to-draw" });
      }
    }
  },

  DISCARD_CARD: async ({ data: { cardId }, dispatch, state }) => {
    const card = state.player.cards.hand.find((c) => c.id === cardId);
    if (card) {
      await dispatch("MOVE_CARD", {
        card,
        from: "hand",
        to: "discard",
      });
    }
  },

  EXHAUST_CARD: async ({ data: { cardId }, dispatch, state }) => {
    const card = state.player.cards.hand.find((c) => c.id === cardId);
    if (card) {
      await dispatch("MOVE_CARD", {
        card,
        from: "hand",
        to: "exhaust",
      });
    }
  },

  ADD_CARD: async ({ data: { card, pile }, dispatch }) => {
    await dispatch("SPAWN_CARD", {
      pile,
      card: card(),
    });
  },
};

export default CardPiles;
