import * as React from "react";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";
import DescartesImage from "~/assets/descartes.png";

import ResourceBars from "./ResourceBars";
import EnemyMechanicLog from "./EnemyMechanicLog";
import EntityBuffs from "./EntityBuffs";

const EnemyStage: React.FC = () => {
  const [{ enemy }] = useFight();

  return (
    <div>
      <Title>Descartes</Title>
      <StageWrapper>
        <AlignRight>
          <EnemyFigure />
          <EntityBuffs target="enemy" />
        </AlignRight>
        <AlignRight>
          <ResourceBars
            target="enemy"
            hp={{
              value: enemy.attributes.hp,
              total: enemy.enemy.baseAttributes.hp,
            }}
            certainty={{
              value: enemy.attributes.certainty,
              total: enemy.enemy.baseAttributes.certainty,
            }}
          />
          <EnemyMechanicLog />
        </AlignRight>
      </StageWrapper>
    </div>
  );
};

const Title = styled.h2`
  text-align: right;
`;

const StageWrapper = styled.div`
  display: flex;
`;

const EnemyFigure = styled.img.attrs({
  src: DescartesImage,
})`
  height: 200px;
`;

const AlignRight = styled.div`
  display: flex;
  flex-direction: column;
  align-items: flex-end;
`

export default EnemyStage;
