import Hooks from "~/fight/hooks.model";
import { FightTarget } from "~/fight/fight.model";
import { nextID } from "~/utils/ids";

export const blankSlate = (target: FightTarget): Hooks.Hook => {
  return {
    id: nextID(),
    p: 1,
    turns: [],
    when: "start-of-turn",
    effects: [
      {
        name: "CLEAR_CERTAINTY",
        data: { target },
      },
    ],
  };
};
