import { Card } from "~/fight/cards.model";
import { nextID } from "~/utils/ids";

export const uncertain = (): Card => {
  const id = nextID();
  return {
    id,
    name: "Uncertain",
    type: "curse",
    cost: 1,
    effects: [
      {
        name: "EXHAUST_CARD",
        data: {
          cardId: id,
        },
      },
    ],
  };
};
