class Deferred<T> {
  public promise: Promise<T>;
  private _resolve: (value?: T | PromiseLike<T> | undefined) => void;
  private _reject: (reason?: any) => void;

  constructor() {
    this._resolve = () => null;
    this._reject = () => null;

    this.promise = new Promise<T>((resolve, reject) => {
      this._resolve = resolve;
      this._reject = reject;
    });
  }

  public resolve(value: T) {
    this._resolve(value);
  }

  public async then(cb: (value: T) => T | void) {
    return this.promise.then(cb);
  }
}

type OptionalUpdate<T> = T | false;
type UpdaterFunction<T> = (
  date: T
) => OptionalUpdate<T> | Promise<OptionalUpdate<T>>;

class UpdaterQueue<T> {
  private queue: Array<[UpdaterFunction<T>, Deferred<OptionalUpdate<T>>]>;

  constructor() {
    this.queue = [];
  }

  public enqueue(updater: UpdaterFunction<T>) {
    const deferred = new Deferred<OptionalUpdate<T>>();
    this.queue.push([updater, deferred]);
    return deferred.promise;
  }

  public async dequeue(data: T) {
    const item = this.queue.shift();
    if (item) {
      const [updater, deferred] = item;
      deferred.resolve(await updater(data));
      return deferred.promise;
    }
    return Promise.reject();
  }

  public async run(data: T, callback?: (state: T) => void) {
    let currentValue: OptionalUpdate<T> = data;
    let lastSuccessfulValue: T = currentValue;

    while (this.queue.length) {

      // TODO: Remove this. This is just to test that we can properly delay events.
      await new Promise((resolve) => {
        setTimeout(resolve, 100);
      });

      currentValue = await this.dequeue(lastSuccessfulValue);
      if (currentValue) {
        lastSuccessfulValue = currentValue;
      }
      if (callback) {
        callback(lastSuccessfulValue);
      }
    }
    return lastSuccessfulValue;
  }

  public async *next(data: T) {
    const item = this.queue.pop();
    if (item) {
      const [updater, deferred] = item;
      deferred.resolve(await updater(data));
      yield await deferred.promise;
    }
    yield Promise.resolve(data);
  }
}

export default UpdaterQueue;
