import { useContext } from "react";

import { createModelContext } from "~/logic/ModelProvider";
import { initFight } from "./FightModel";

const FightContext = createModelContext(
  initFight(
    {
      deck: [],
      energy: 0,
      baseAttributes: {
        hp: 0,
        certainty: 0,
      },
    },
    {
      name: "Loading...",
      baseAttributes: {
        hp: 0,
        certainty: 0,
      },
      baseHooks: [],
    }
  )
);

export const useFight = () => {
  return useContext(FightContext);
};

export default FightContext;
