import UpdaterQueue from "./queue";

// Models update their state with actions
// Actions have a `name` and optionally some `data`
export type Action<TName, TData = {}> = { name: TName; data: TData };

// The action map tells us which keys map to which actions
export interface BaseActionsMap {
  [name: string]: Action<string>;
}

export type ModelStateUpdate<S> = S | false;
export type QueuedStateUpdate<S> = Promise<ModelStateUpdate<S>>;

// The dispatch function is passed to effect handlers to run multiple actions
export interface DispatchFunction<S, A extends BaseActionsMap> {
  <K extends keyof A>(name: K, data: A[K]["data"]): QueuedStateUpdate<S>;
}

// Configuration for action handlers
export type ActionHandlers<S, A extends BaseActionsMap> = {
  [K in keyof A]?: (state: S, data: A[K]["data"]) => ModelStateUpdate<S>;
};

// Configuration for effect handlers
export type EffectHandlers<
  S,
  A extends BaseActionsMap,
  E extends BaseActionsMap
> = {
  [K in keyof E]?: (options: {
    data: E[K]["data"];
    dispatch: DispatchFunction<S, A>;
    affect: DispatchFunction<S, E>;
    state: S;
  }) => void;
};

class Model<S, A extends BaseActionsMap, E extends BaseActionsMap> {
  private state: S;
  private actions: ActionHandlers<S, A>;
  private effects: EffectHandlers<S, A, E>;
  private queue: UpdaterQueue<ModelStateUpdate<S>>;
  private subscriptions: Array<(state: S) => void>;
  private effectSubscriptions: Map<
    keyof E,
    Array<(data: E[keyof E]["data"], state: S) => void>
  >;
  private actionSubscriptions: Map<
    keyof A,
    Array<(data: A[keyof A]["data"], state: S) => void>
  >;

  constructor(options: {
    state: S;
    actions: ActionHandlers<S, A>;
    effects: EffectHandlers<S, A, E>;
  }) {
    this.state = options.state;
    this.actions = options.actions;
    this.effects = options.effects;
    this.queue = new UpdaterQueue();
    this.subscriptions = [];
    this.effectSubscriptions = new Map();
    this.actionSubscriptions = new Map();
  }

  public async run(): Promise<ModelStateUpdate<S>> {
    this.log(" Running", this.state);
    return await this.queue.run(this.state, (state) => {
      if (state) {
        this.setState(state);
        return state;
      }
      return this.state;
    });
  }

  public getState() {
    return this.state;
  }

  public subscribe(subscriptionCb: (state: S) => void) {
    this.subscriptions.push(subscriptionCb);
  }

  public subscribeToAction<K extends keyof A>(
    name: K,
    subscriptionCb: (data: A[K]["data"], state: S) => void
  ) {
    const subscriptions = this.actionSubscriptions.get(name);
    if (subscriptions) {
      subscriptions.push(subscriptionCb);
    } else {
      this.actionSubscriptions.set(name, [subscriptionCb]);
    }
  }

  public subscribeToEffect<K extends keyof E>(
    name: K,
    subscriptionCb: (data: E[K]["data"], state: S) => void
  ) {
    const subscriptions = this.effectSubscriptions.get(name);
    if (subscriptions) {
      subscriptions.push(subscriptionCb);
    } else {
      this.effectSubscriptions.set(name, [subscriptionCb]);
    }
  }

  public dispatchAction<K extends keyof A>(
    name: K,
    data: A[K]["data"]
  ): QueuedStateUpdate<S> {
    const action = this.actions[name];
    if (action) {
      this.log(" Queuing Action", name, data);

      return this.queue.enqueue((state) => {
        this.log(" Calling Action", name, data);
        this.notifyActionSubscribers(name, data, state || this.state);
        return action(state || this.state, data);
      });
    }
    return Promise.resolve(this.state);
  }

  public dispatchEffect<K extends keyof E>(
    name: K,
    data: E[K]["data"]
  ): QueuedStateUpdate<S> {
    const effect = this.effects[name];
    if (effect) {
      this.log(" Queuing Effect", name, data);

      return this.queue.enqueue((state) => {
        this.log(" Running Effect", name, data);
        this.notifyEffectSubscribers(name, data, state || this.state);
        effect({
          data,
          dispatch: this.dispatchAction.bind(this),
          affect: this.dispatchEffect.bind(this),
          state: state || this.state,
        });
        return this.run();
      });
    }
    return Promise.resolve(this.state);
  }

  private notifyActionSubscribers<K extends keyof A>(
    name: K,
    data: A[K]["data"],
    state: S
  ) {
    const subscriptions = this.actionSubscriptions.get(name);
    if (subscriptions) {
      for (const subscription of subscriptions) {
        subscription(data, state);
      }
    }
  }

  private notifyEffectSubscribers<K extends keyof E>(
    name: K,
    data: E[K]["data"],
    state: S
  ) {
    const subscriptions = this.effectSubscriptions.get(name);
    if (subscriptions) {
      for (const subscription of subscriptions) {
        subscription(data, state);
      }
    }
  }

  private setState(state: S) {
    this.state = state;
    for (const subscription of this.subscriptions) {
      subscription(state);
    }
  }

  private log(...messages: any[]) {
    console.log("[MODEL]", ...messages);
  }
}

export default Model;
