import { Action, ActionHandlers, EffectHandlers } from "~/logic/model";

import Entity, {
  actions as entityActions,
  effects as entityEffects,
} from "./entity.model";
import CardPiles, {
  Card,
  actions as cardActions,
  effects as cardEffects,
} from "./cards.model";
import Fight from "./fight.model";

/**
 * Module for the player. Extends `Entity` and `CardPiles` and provides the
 * ability to set the player's energy, play cards, and end the turn.
 */
declare namespace Player {
  type Player = {
    deck: Card[];
    energy: number;
    baseAttributes: Entity.State["attributes"];
  };

  type State = Entity.State &
    CardPiles.State & {
      energy: number;
      player: Player;
      canPlay: boolean;
    };

  type Actions = Entity.Actions &
    CardPiles.Actions & {
      // Informs us if player can perform actions right now
      SET_PLAYER_CAN_PLAY: Action<
        "SET_PLAYER_CAN_PLAY",
        {
          canPlay: boolean;
        }
      >;
      // Sets the player's energy
      SET_ENERGY: Action<
        "SET_ENERGY",
        {
          energy: number;
        }
      >;
    };

  type Effects = Entity.Effects &
    CardPiles.Effects & {
      // Plays a card, running its effects and discarding it
      PLAY_CARD: Action<
        "PLAY_CARD",
        {
          card: Card;
        }
      >;
      START_TURN: Action<"START_TURN">;
      END_TURN: Action<"END_TURN">;
    };
}

export const actions: ActionHandlers<Fight.State, Fight.Actions> = {
  ...entityActions,
  ...cardActions,

  SET_PLAYER_CAN_PLAY: (state, { canPlay }) => {
    return {
      ...state,
      player: { ...state.player, canPlay },
    };
  },

  SET_ENERGY: (state, { energy }) => {
    return {
      ...state,
      player: { ...state.player, energy },
    };
  },
};

export const effects: EffectHandlers<
  Fight.State,
  Fight.Actions,
  Fight.Effects
> = {
  ...entityEffects,
  ...cardEffects,

  START_TURN: async ({ dispatch, affect, state }) => {
    await dispatch("INCREMENT_TURN", {});
    await dispatch("SET_ENERGY", {
      energy: state.player.player.energy,
    });

    await affect("RUN_HOOKS", {
      hooks: state.player.hooks.filter((hook) => hook.when === "start-of-turn"),
    });

    await affect("DRAW_CARDS", { nCards: 5 });
    await dispatch("SET_PLAYER_CAN_PLAY", {
      canPlay: true,
    });
  },

  END_TURN: async ({ dispatch, affect, state }) => {
    await dispatch("SET_PLAYER_CAN_PLAY", {
      canPlay: false,
    });
    await affect("RUN_HOOKS", {
      hooks: state.player.hooks.filter((hook) => hook.when === "end-of-turn"),
    });

    for (const card of state.player.cards.hand) {
      await dispatch("MOVE_CARD", {
        card,
        from: "hand",
        to: "discard",
      });
    }
    await affect("RUN_EMENY_AI", {});
    await affect("START_TURN", {});
  },

  PLAY_CARD: async ({ data: { card }, dispatch, affect, state }) => {
    await dispatch("SET_ENERGY", {
      energy: state.player.energy - card.cost,
    });

    for (const effect of card.effects) {
      await affect(effect.name, effect.data);
    }

    await dispatch("MOVE_CARD", {
      card,
      from: "hand",
      to: "discard",
    });
  },
};

export default Player;
