import { shuffle } from "lodash";

import Model from "~/logic/model";
import * as baseHooks from "~/game/baseHooks";

import Fight, { actions, effects } from "./fight.model";
import Enemy from "./enemy.model";
import Player from "./player.model";

export const initFight = (player: Player.Player, enemy: Enemy.Enemy) => {
  return new Model<Fight.State, Fight.Actions, Fight.Effects>({
    state: {
      turn: 0,
      player: {
        player,
        canPlay: false,
        energy: 3,
        attributes: {
          ...player.baseAttributes,
        },
        hooks: [baseHooks.blankSlate("player")],
        stance: null,
        powers: [],
        cards: {
          hand: [],
          draw: shuffle(player.deck),
          discard: [],
          exhaust: [],
        },
      },
      enemy: {
        enemy,
        attributes: {
          ...enemy.baseAttributes,
        },
        hooks: [baseHooks.blankSlate("enemy"), ...enemy.baseHooks],
        stance: null,
        powers: [],
      },
    },
    actions,
    effects,
  });
};
