import { Action, ActionHandlers, EffectHandlers } from "~/logic/model";

import Player, {
  actions as playerActions,
  effects as playerEffects,
} from "./player.model";
import Enemy, {
  actions as enemyActions,
  effects as enemyEffects,
} from "./enemy.model";

export type FightTarget = "player" | "enemy";

/**
 * Root module for fight
 */
declare namespace Fight {
  type State = {
    turn: number;
    player: Player.State;
    enemy: Enemy.State;
  };

  type Actions = Player.Actions &
    Enemy.Actions & {
      INCREMENT_TURN: Action<"INCREMENT_TURN">;
    };

  type Effects = Player.Effects &
    Enemy.Effects & {
      START_FIGHT: Action<"START_FIGHT">;
    };
}

export const actions: ActionHandlers<Fight.State, Fight.Actions> = {
  ...playerActions,
  ...enemyActions,

  INCREMENT_TURN: (state) => {
    return {
      ...state,
      turn: state.turn + 1,
    };
  },
};

export const effects: EffectHandlers<
  Fight.State,
  Fight.Actions,
  Fight.Effects
> = {
  ...playerEffects,
  ...enemyEffects,

  START_FIGHT: async ({ affect }) => {
    await affect("START_TURN", {});
  },
};

export default Fight;
