import { Action, ActionHandlers, EffectHandlers } from "~/logic/model";

import Fight from "./fight.model";
import Entity, {
  actions as entityActions,
  effects as entityEffects,
} from "./entity.model";

/**
 * Module for the enemy
 */
declare namespace Enemy {
  type Enemy = {
    name: string;
    baseAttributes: Entity.State["attributes"];
    baseHooks: Enemy.State["hooks"];
  };

  type Mechanic = {
    name: string;
    effects: Fight.Effects[keyof Fight.Effects][];
  };

  type State = Entity.State & {
    enemy: Enemy;
  };

  type Actions = Entity.Actions

  type Effects = Entity.Effects & {
    RUN_EMENY_AI: Action<"RUN_EMENY_AI">;
    RUN_MECHANIC: Action<
      "RUN_MECHANIC",
      {
        mechanic: Mechanic;
      }
    >;
  };
}

export const actions: ActionHandlers<Fight.State, Fight.Actions> = {
  ...entityActions,
};

export const effects: EffectHandlers<
  Fight.State,
  Fight.Actions,
  Fight.Effects
> = {
  ...entityEffects,

  RUN_EMENY_AI: async ({ affect, state }) => {
    await affect("RUN_HOOKS", {
      hooks: state.enemy.hooks.filter((hook) => hook.when === "start-of-turn"),
    });
    await affect("RUN_HOOKS", {
      hooks: state.enemy.hooks.filter((hook) => hook.when === "end-of-turn"),
    });
  },

  RUN_MECHANIC: async ({ affect, data: { mechanic } }) => {
    for (const effect of mechanic.effects) {
      await affect(effect.name, effect.data);
    }
  },
};

export default Enemy;
