import { capitalize } from "lodash";
import React from "react";
import styled from "styled-components";

import { FightTarget } from "~/fight/fight.model";
import Hooks from "~/fight/hooks.model";
import EffectsText from "./EffectsText";

const getHookEnglish = (hook: Hooks.Hook) => {
  let english: string[] = [];

  // turn definition
  switch (hook.turns[0]) {
    case "before":
    case "after":
      /**
       * e.g.
       * - "Before turn 10"
       * - "After turn 5"
       */
      english.push(`${hook.turns[0]} turn ${hook.turns[1]}`);
      break;
    default:
      /**
       * e.g.
       * - "On turn 1"
       * - "On turns 1 and 2"
       * - "On turns 1, 2, and 3"
       */
      if (hook.turns.length > 0) {
        let turnEnglish = `on turn${hook.turns.length > 1 ? "s" : ""} `;
        if (hook.turns.length >= 2) {
          let [last, ...first] = [...hook.turns].reverse();
          first.reverse();
          turnEnglish += `${first.join(", ")} and ${last}`;
        } else {
          turnEnglish += `${hook.turns[0]}`;
        }
        english.push(turnEnglish);
      } else {
        english.push("every turn");
      }
      break;
  }

  // when definition
  switch (hook.when) {
    case "start-of-turn":
      english.push("at the start of the turn");
      break;
    case "end-of-turn":
      english.push("at the end of the turn");
      break;
  }

  // chance definition
  if (hook.p < 1) {
    english.push(`with a ${hook.p * 100}% chance`);
  }

  return capitalize(english.join(", "));
};

interface HooksTextProps {
  hooks: Array<Hooks.Hook>;
  target?: FightTarget;
}

const HooksText: React.FC<HooksTextProps> = ({ hooks, target }) => {
  return (
    <HooksList>
      {hooks.map((hook, i) => {
        return (
          <HookListItem key={i}>
            <HookText>{getHookEnglish(hook)}:</HookText>
            <EffectsText effects={hook.effects} target={target} />
          </HookListItem>
        );
      })}
    </HooksList>
  );
};

const HookText = styled.p`
  font-style: italic;
  text-align: center;
`

const HooksList = styled.div``;

const HookListItem = styled.div`
  font-size: 0.85em;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.05);
  margin-top: 5px;
  padding: 5px;
`

export default HooksText;
