import * as React from "react";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";
import { FightTarget } from "~/fight/fight.model";

import VerticalBar from "~/components/ui/VerticalBar";

interface ResourceBarsProps {
  target: FightTarget;
  hp?: { value: number; total: number };
  certainty?: { value: number; total: number };
}

const ResourceBars: React.FC<ResourceBarsProps> = ({
  target,
  hp = { value: 100, total: 100 },
  certainty = { value: 0, total: 100 },
}) => {
  const [{}, model] = useFight();
  const [hpDeltas, setHpDeltas] = React.useState<number[]>([]);
  const [certaintyDeltas, setCertaintyDeltas] = React.useState<number[]>([]);

  React.useEffect(() => {
    model.subscribeToEffect("DEAL_DAMAGE", (data) => {
      if (data.target !== target) return;
      setHpDeltas((deltas) => [...deltas, -data.damage]);
    });

    model.subscribeToEffect("GAIN_CERTAINTY", (data) => {
      if (data.target !== target) return;
      setCertaintyDeltas((deltas) => [...deltas, data.certainty]);
    });

    // Listening to the individual "SET_ATTRIBUTES" is more accurate, but
    // unfortunately shows things like "CLEAR_CERTAINTY", which happens every turn.

    // model.subscribeToAction("SET_ATTRIBUTE", (data, state) => {
    //   if (data.target !== target) return;
    //   const currentValue = state[target].attributes[data.attribute];
    //   const newDelta = data.value - currentValue;

    //   switch (data.attribute) {
    //     case "hp":
    //       setHpDeltas((deltas) => [...deltas, newDelta]);
    //       break;
    //     case "certainty":
    //       setCertaintyDeltas((deltas) => [...deltas, newDelta]);
    //       break;
    //   }
    // });
  }, []);

  return (
    <ResourceBarsWrapper>
      <VerticalBar
        label="Resolve"
        value={hp.value}
        total={hp.total}
        color="crimson"
        deltas={hpDeltas}
      />
      <VerticalBar
        label="Certainty"
        value={certainty.value}
        total={100}
        color="dodgerblue"
        deltas={certaintyDeltas}
      />
    </ResourceBarsWrapper>
  );
};

const ResourceBarsWrapper = styled.div`
  display: flex;
`;

export default ResourceBars;
