import * as React from "react";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";
import CardPile from "./CardPile";

const FightStageFooter: React.FC = () => {
  const [state, model] = useFight();
  const endTurn = () => {
    model.affect("END_TURN", {});
    model.run();
  };
  return (
    <Wrapper>
      <Section>
        <EnergyCount>
          {state.player.energy}
          {" / "}
          {state.player.player.energy}
        </EnergyCount>
        <CardPile name="draw" cards={state.player.cards.draw} />
      </Section>
      <Section>
        <EndTurnButton
          disabled={!state.player.canPlay}
          onClick={endTurn}
        >
          End Turn
        </EndTurnButton>
        <CardPile name="discard" cards={state.player.cards.discard} />
      </Section>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
`;

const Section = styled.div`
  position: relative;
`;

const EnergyCount = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 16px;
  margin-bottom: 16px;
  color: black;
  font-size: 1.75rem;
  font-weight: bold;
  width: 80px;
  height: 80px;
  border-radius: 100%;
  background: gold;
  box-shadow: 0 0 16px gold;
`;

const EndTurnButton = styled.button`
  display: block;
  font-size: 1.2em;
  font-weight: bold;
  font-family: Philosopher, serif;
  text-transform: uppercase;
  padding: 16px;
  border: none;
  border-bottom: 2px solid rgba(0, 0, 0, 0.15);
  appearance: none;
  background-color: tomato;
  border-radius: 4px;
  margin-bottom: 16px;
  cursor: pointer;

  opacity: ${(props) => (props.disabled ? 0.5 : 1)};
  pointer-events: ${(props) => (props.disabled ? "none" : "")};
`;

export default FightStageFooter;
