import React from "react";
import styled from "styled-components";
import ReactTooltip from "react-tooltip";

import { useFight } from "~/fight/FightContext";
import Enemy from "~/fight/enemy.model";
import EffectsText from "~/components/ui/EffectsText";

const EnemyMechanicLog: React.FC = () => {
  const [played, setPlayed] = React.useState<Array<[number, Enemy.Mechanic]>>(
    []
  );
  const [, model] = useFight();

  React.useEffect(() => {
    model.subscribeToEffect("RUN_MECHANIC", (data, state) => {
      setPlayed((prev) => [[state.turn, data.mechanic], ...prev]);
    });
  }, []);

  return (
    <GameLog>
      {played.map(([turn, mechanic], i) => {
        const id = `${turn}:${mechanic.name}:${i}`;
        return (
          <React.Fragment key={id}>
            <ReactTooltip
              id={id}
              effect="solid"
              place="left"
              backgroundColor="white"
              textColor="#333333"
              multiline
            >
              <MechanicName>{mechanic.name}</MechanicName>
              <EffectsText
                effects={mechanic.effects}
                target="enemy"
                showHooks
              />
            </ReactTooltip>
            <GameLogItem data-tip data-for={id}>
              <GameLogItemHeader>
                <span>{i === 0 ? " Last Action" : null}</span>
                <span>Turn {turn}</span>
              </GameLogItemHeader>
              <GameLogMechanicName>{mechanic.name}</GameLogMechanicName>
            </GameLogItem>
          </React.Fragment>
        );
      })}
    </GameLog>
  );
};

const GameLog = styled.div`
  display: flex;
  flex-direction: column;
  max-height: 150px;
  min-width: 200px;
  overflow-y: auto;
`;

const GameLogItem = styled.div`
  padding: 5px;
  margin-bottom: 5px;
  background: rgba(0, 0, 0, 0.66);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
`;

const MechanicName = styled.span`
  display: block;
  font-weight: bold;
  font-size: 1.15em;
  text-align: right;
  margin-bottom: 5px;
`;

const GameLogMechanicName = styled.span``

const GameLogItemHeader = styled.div`
  font-size: 0.66em;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 5px;
`;

export default EnemyMechanicLog;
