import Entity from "~/fight/entity.model";

import { uncertain } from "~/game/cards/curses";
import { nextID } from "~/utils/ids";


export const methodologicalDoubt = (): Entity.Stance => {
  return {
    name: "Methodological Doubt",
    hooks: [
      {
        id: nextID(),
        p: 1,
        turns: [],
        when: "start-of-turn",
        effects: [
          {
            name: "ADD_CARD",
            data: {
              card: uncertain,
              pile: "draw",
            },
          },
        ],
      },
    ],
  };
};

export const clearAndDistinct = (): Entity.Stance => {
  return {
    name: "Clear and Distinct",
    hooks: [
      {
        id: nextID(),
        p: 1,
        turns: [],
        when: "end-of-turn",
        effects: [
          {
            name: "GAIN_CERTAINTY",
            data: {
              target: "enemy",
              certainty: 5,
            },
          },
          {
            name: "GAIN_CERTAINTY",
            data: {
              target: "player",
              certainty: 5,
            },
          },
        ],
      },
    ],
  };
};
