import React from "react";
import ReactTooltip from "react-tooltip";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";
import { FightTarget } from "~/fight/fight.model";
import HooksText from "~/components/ui/HooksText";
import MethodologicalDoubt from "~/assets/methodological-doubt.png";
import ClearAndDistinct from "~/assets/clear-and-distinct.png";

const STANCE_ICONS: Map<string, string> = new Map([
  ["Methodological Doubt", MethodologicalDoubt],
  ["Clear and Distinct", ClearAndDistinct],
]);

type EntityStanceProps = {
  target: FightTarget;
};
const EntityStance: React.FC<EntityStanceProps> = ({ target }) => {
  const [state] = useFight();
  const { stance } = state[target];
  if (!stance) return null;

  const id = `${target}:${stance.name}`;
  const icon = STANCE_ICONS.get(stance.name) || "";

  return (
    <>
      <ReactTooltip
        id={id}
        effect="solid"
        place="bottom"
        backgroundColor="white"
        textColor="#333333"
        multiline
      >
        {/* <MetaTitle>Stance</MetaTitle> */}
        <StanceName>{stance.name}</StanceName>
        <HooksText target="enemy" hooks={stance.hooks} />
      </ReactTooltip>
      <StanceIcon
        data-tip
        data-for={id}
        style={{ backgroundImage: `url(${icon})` }}
      />
    </>
  );
};

const MetaTitle = styled.div`
  text-align: center;
`

const StanceName = styled.div`
  font-weight: bold;
  text-align: center;
`;

const StanceIcon = styled.div`
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 5px;
  border-radius: 100%;
  background-size: cover;
  background-position: center;
`;

export default EntityStance;
