import { capitalize, noop } from "lodash";
import React from "react";
import styled from "styled-components";
import { motion } from "framer-motion";

import { Card } from "~/fight/cards.model";
import { useFight } from "~/fight/FightContext";
import PaperCard from "~/components/ui/PaperCard";
import FadeAwayValue from "~/components/ui/FadeAwayValue";

interface CardPileProps {
  name: "draw" | "discard";
  cards: Card[];
}
const CardPile: React.FC<CardPileProps> = ({ name, cards }) => {
  const [{}, model] = useFight();
  const [cardsAdded, setCardsAdded] = React.useState<string[]>([]);

  React.useEffect(() => {
    model.subscribeToEffect("ADD_CARD", (data) => {
      if (data.pile !== name) return;
      setCardsAdded((prev) => [...prev, data.card.name]);
    });
  }, []);

  return (
    <CardPileWrapper>
      <AddedCardsWrapper>
        {cardsAdded.map((card, i) => (
          <FadeAwayValue key={i} duration={1.5}>
            <AddedCardText>
              {`+1 ${capitalize(card)}`}
            </AddedCardText>
          </FadeAwayValue>
        ))}
      </AddedCardsWrapper>
      <PileTitle>
        {capitalize(name)} Pile: {cards.length}
      </PileTitle>
      <Pile>
        {cards.map((card, i) => {
          return (
            <AnimatedCardWrapper
              key={card.id}
              layoutId={`${card.id}`}
              animate={{
                scale: 0.1,
                rotate: (name === "draw" ? -1 : 1) * 270,
                y: 0,
                // TODO: Setup themes
                backgroundColor: "#ffd700",
              }}
              transition={{ duration: 1 }}
              initial={false}
            >
              <PaperCard card={card} onClick={noop} inTransition />
            </AnimatedCardWrapper>
          );
        })}
      </Pile>
    </CardPileWrapper>
  );
};

const CardPileWrapper = styled.div`
  position: relative;
`;

const AddedCardsWrapper = styled.div`
  position: absolute;
  top: -100px;
  left: 20px;
`;

const AddedCardText = styled.div`
  color: plum;
  display: inline-block;
  white-space: nowrap;
`

const Pile = styled.div`
  position: relative;
`;

const PileTitle = styled.div`
  color: #333;
  padding: 10px;
  border-radius: 4px 4px 0 0;
  border-bottom: 2px solid rgba(0, 0, 0, 0.15);
  background: white;
`;

const AnimatedCardWrapper = styled(motion.div)`
  position: absolute;
  bottom: 0;
`;

export default CardPile;
