import * as React from "react";
import styled from "styled-components";

import { useFight } from "~/fight/FightContext";
import PlayerImage from "~/assets/player.png";
import ResourceBars from "./ResourceBars";

const PlayerStage: React.FC = () => {
  const [{ player }] = useFight();
  return (
    <div>
      <h2>Player</h2>
      <StageWrapper>
        <ResourceBars
          target="player"
          hp={{
            value: player.attributes.hp,
            total: player.player.baseAttributes.hp,
          }}
          certainty={{
            value: player.attributes.certainty,
            total: player.player.baseAttributes.certainty,
          }}
        />
        <PlayerFigure />
      </StageWrapper>
    </div>
  );
};

const StageWrapper = styled.div`
  display: flex;
`;

const PlayerFigure = styled.img.attrs({
  src: PlayerImage,
})`
  height: 200px;
`;

export default PlayerStage;
