import React, { useState, useEffect, createContext, useContext } from "react";

import Model, {
  BaseActionsMap,
  DispatchFunction,
  QueuedStateUpdate,
} from "./model";

type ModelBundle<S, A extends BaseActionsMap, E extends BaseActionsMap> = {
  dispatch: DispatchFunction<S, A>;
  affect: DispatchFunction<S, E>;
  run: () => QueuedStateUpdate<S>;
  subscribeToEffect: <K extends keyof E>(
    name: K,
    cb: (data: E[K]["data"], state: S) => void
  ) => void;
  subscribeToAction: <K extends keyof A>(
    name: K,
    cb: (data: A[K]["data"], state: S) => void
  ) => void;
};

export const useModel = <S, A extends BaseActionsMap, E extends BaseActionsMap>(
  model: Model<S, A, E>
): [S, ModelBundle<S, A, E>] => {
  const [state, setState] = useState<S>(model.getState());
  const [bundle] = useState<ModelBundle<S, A, E>>({
    dispatch: model.dispatchAction.bind(model),
    affect: model.dispatchEffect.bind(model),
    run: model.run.bind(model),
    subscribeToEffect: model.subscribeToEffect.bind(model),
    subscribeToAction: model.subscribeToAction.bind(model),
  });

  useEffect(() => {
    model.subscribe((nextState) => {
      setState(nextState);
    });
  }, []);

  return [state, bundle];
};

export const createModelContext = <
  S,
  A extends BaseActionsMap,
  E extends BaseActionsMap
>(
  model: Model<S, A, E>
) => {
  const Context = createContext<[S, ModelBundle<S, A, E>]>([
    model.getState(),
    {
      dispatch: model.dispatchAction.bind(model),
      affect: model.dispatchEffect.bind(model),
      run: model.run.bind(model),
      subscribeToEffect: model.subscribeToEffect.bind(model),
      subscribeToAction: model.subscribeToAction.bind(model),
    },
  ]);
  return Context;
};
