import Enemy from "~/fight/enemy.model";

import * as hooks from "./hooks"

export const createDescartes = (): Enemy.Enemy => {
  return {
    name: "Descartes",
    baseAttributes: {
      hp: 100,
      certainty: 0,
    },
    baseHooks: [
      hooks.raze(),
      hooks.dreamArgument(),
      hooks.geniumMalignum(),
      hooks.doubt(),
      hooks.insight(),
      hooks.cogito(),
      hooks.clearAndDistinct(),
    ],
  };
};
