import Hooks from "~/fight/hooks.model";
import { nextID } from "~/utils/ids";

import * as mechanics from "./mechanics";

export const raze = (): Hooks.Hook => {
  return {
    id: nextID(),
    p: 1,
    turns: [1],
    when: "start-of-turn",
    effects: [
      {
        name: "RUN_MECHANIC",
        data: {
          mechanic: mechanics.raze,
        },
      },
    ],
  };
};

